import React from "react";
import { Button, Form } from "react-bootstrap";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import { createPatientHistory, getPatientHistory, updatePatientHistory } from "../../app/features/patientHistory/patientHistory.slice";
import { FieldError } from "../ErrorMessage/ErrorMessage";

const CurrentMedication = ({ setEditing = false }) => {
  const user = JSON.parse(localStorage.getItem("family_doc_app"));
  const { patientHistoryData } = useSelector((state) => state.patientHistory);

  const dispatch = useDispatch();

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const moveToNextMedicalHistory = () => {
    const finalData = {
      patientId: user?.userId,
    };
    dispatch(getPatientHistory(finalData));
    setEditing(false);
  };

  function onSubmit(data) {
    const finalData = {
      patientId: user?.userId,
      historyTypeId: 2,
      pastMedicalHistory: null,
      currentMedical: data?.currentMedical,
      allergyHistory: null,
    };
    if (patientHistoryData?.historyTypeId) {
      dispatch(updatePatientHistory({ finalData, moveToNextMedicalHistory }));
    } else {
      dispatch(createPatientHistory({ finalData, moveToNextMedicalHistory }));
    }
  }

  return (
    <Form onSubmit={handleSubmit(onSubmit)}>
      <div className="accordion_content position-relative">
        <Form.Group className="mb-3">
          <Form.Control
            name="currentMedical"
            as="textarea"
            defaultValue={patientHistoryData?.currentMedical ?? ""}
            rows={12}
            {...register("currentMedical", {
              required: !patientHistoryData?.currentMedical,
            })}
          />
          {errors.currentMedical && (
            <FieldError message={"This Field is Required"} />
          )}
        </Form.Group>
      </div>
      <div className="d-flex justify-content-end">
        <Button className="px-4 py-2 primary_bg" size="sm" type="submit">
          Save
        </Button>
      </div>
    </Form>
  );
};

export default CurrentMedication;
