import React, { useState, useEffect } from "react";
import { Accordion, Button } from "react-bootstrap";
import { AiOutlineLeft, AiOutlineRight } from "react-icons/ai";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { BsPencilSquare } from "react-icons/bs";
import { RiDeleteBin5Fill } from "react-icons/ri";
import {
  deletePatientHistory,
  getPatientHistory,
} from "../../../../app/features/patientHistory/patientHistory.slice";
import ProfileLayout from "../../../../components/ProfileLayout/CompleteProfile";
import { PATH } from "../../../../utils/path";
import { ConfirmationModal } from "../../../../components/Modal/ConfirmationModal";
import PastMedicalHistory from "../../../../components/PastMedicalHistory";
import CurrentMedication from "../../../../components/CurrentMedication";
import AllergyHistory from "../../../../components/AllergyHistory";

const MedicalHistory = () => {
  const user = JSON.parse(localStorage.getItem("family_doc_app"));
  const { patientHistoryData } = useSelector((state) => state.patientHistory);
  const [deleteModel, setDeleteModel] = useState(false);
  const [deleteId, setDeleteId] = useState("");
  const [editing, setEditing] = useState({
    pastMedical: false,
    currentMedication: false,
    allergy: false,
  });
  const dispatch = useDispatch();
  const navigate = useNavigate();

  useEffect(() => {
    const finalData = {
      patientId: user?.userId,
    };
    dispatch(getPatientHistory(finalData));
  }, [dispatch, user?.userId]);

  /** Delete Medical */
  const moveToNextDeleteMedicalHistory = () => {
    const finalData = {
      patientId: user?.userId,
    };
    dispatch(getPatientHistory(finalData));
  };
  const onDelete = () => {
    const finalData = {
      patientId: user?.userId,
      historyTypeId: deleteId ? deleteId : null,
    };
    dispatch(
      deletePatientHistory({ finalData, moveToNextDeleteMedicalHistory })
    );
  };
  return (
    <>
      <ProfileLayout className="pt-5">
        <div className="mx-auto width_75 medical_history">
          <div className="width_75 mx-auto">
            <Accordion className="mb-3" defaultActiveKey="0">
              <Accordion.Item
                className="border-0 mb-3 accordion_item"
                eventKey="0"
              >
                <Accordion.Header className="accordion_header fw-semibold">
                  Past Medical History
                  <span style={{ color: "#FF3A3A" }} className="fw-bold">
                    *
                  </span>
                </Accordion.Header>
                <Accordion.Body>
                  {patientHistoryData?.pastMedicalHistory &&
                  !editing.pastMedical ? (
                    <div className="d-flex justify-content-between py-3">
                      <div className="pe-2">
                        <p
                          style={{
                            textAlign: "justify",
                            marginRight: "10px",
                          }}
                        >
                          {patientHistoryData?.pastMedicalHistory}
                        </p>
                      </div>
                      <div>
                        <div
                          className="d-flex justify-content-center align-items-center mb-2"
                          style={{
                            height: "29px",
                            width: "32px",
                            backgroundColor: "#E1EBFF",
                            borderRadius: "5px",
                            cursor: "pointer",
                          }}
                          onClick={() =>
                            setEditing((prev) => ({
                              ...prev,
                              pastMedical: true,
                            }))
                          }
                        >
                          <BsPencilSquare
                            style={{
                              color: "#2269F2",
                              fontSize: "18px",
                            }}
                          />
                        </div>
                        <div
                          className="d-flex justify-content-center align-items-center"
                          style={{
                            height: "29px",
                            width: "32px",
                            backgroundColor: "#FFDADD",
                            borderRadius: "5px",
                            cursor: "pointer",
                          }}
                          onClick={() => {
                            setDeleteModel(true);
                            setDeleteId(1);
                          }}
                        >
                          <RiDeleteBin5Fill
                            style={{
                              color: "#E63946",
                              fontSize: "18px",
                            }}
                          />
                        </div>
                      </div>
                    </div>
                  ) : (
                    <PastMedicalHistory
                      setEditing={(status) =>
                        setEditing((prev) => ({ ...prev, pastMedical: status }))
                      }
                    />
                  )}
                </Accordion.Body>
              </Accordion.Item>
              <Accordion.Item
                className="border-0 mb-3 accordion_item"
                eventKey="1"
              >
                <Accordion.Header className="accordion_header fw-semibold">
                  Current Medication
                  <span style={{ color: "#FF3A3A" }} className="fw-bold">
                    *
                  </span>
                </Accordion.Header>
                <Accordion.Body>
                  {patientHistoryData?.currentMedical &&
                  !editing.currentMedication ? (
                    <div className="d-flex justify-content-between py-3">
                      <div className="pe-2">
                        <p
                          style={{
                            textAlign: "justify",
                            marginRight: "10px",
                          }}
                        >
                          {patientHistoryData?.currentMedical}
                        </p>
                      </div>
                      <div>
                        <div
                          className="d-flex justify-content-center align-items-center mb-2"
                          style={{
                            height: "29px",
                            width: "32px",
                            backgroundColor: "#E1EBFF",
                            borderRadius: "5px",
                            cursor: "pointer",
                          }}
                          onClick={() =>
                            setEditing((prev) => ({
                              ...prev,
                              currentMedication: true,
                            }))
                          }
                        >
                          <BsPencilSquare
                            style={{
                              color: "#2269F2",
                              fontSize: "18px",
                            }}
                          />
                        </div>
                        <div
                          className="d-flex justify-content-center align-items-center"
                          style={{
                            height: "29px",
                            width: "32px",
                            backgroundColor: "#FFDADD",
                            borderRadius: "5px",
                            cursor: "pointer",
                          }}
                          onClick={() => {
                            setDeleteModel(true);
                            setDeleteId(2);
                          }}
                        >
                          <RiDeleteBin5Fill
                            style={{
                              color: "#E63946",
                              fontSize: "18px",
                            }}
                          />
                        </div>
                      </div>
                    </div>
                  ) : (
                    <CurrentMedication
                      setEditing={(status) =>
                        setEditing((prev) => ({
                          ...prev,
                          currentMedication: status,
                        }))
                      }
                    />
                  )}
                </Accordion.Body>
              </Accordion.Item>
              <Accordion.Item
                className="border-0 mb-3 accordion_item"
                eventKey="2"
              >
                <Accordion.Header className="accordion_header fw-semibold">
                  Allergy History (to any Medication)
                  <span style={{ color: "#FF3A3A" }} className="fw-bold">
                    *
                  </span>
                </Accordion.Header>
                <Accordion.Body>
                  {patientHistoryData?.allergyHistory && !editing.allergy ? (
                    <div className="d-flex justify-content-between py-3">
                      <div className="pe-2">
                        <p
                          style={{
                            textAlign: "justify",
                            marginRight: "10px",
                          }}
                        >
                          {patientHistoryData?.allergyHistory}
                        </p>
                      </div>
                      <div>
                        <div
                          className="d-flex justify-content-center align-items-center mb-2"
                          style={{
                            height: "29px",
                            width: "32px",
                            backgroundColor: "#E1EBFF",
                            borderRadius: "5px",
                            cursor: "pointer",
                          }}
                          onClick={() =>
                            setEditing((prev) => ({ ...prev, allergy: true }))
                          }
                        >
                          <BsPencilSquare
                            style={{
                              color: "#2269F2",
                              fontSize: "18px",
                            }}
                          />
                        </div>
                        <div
                          className="d-flex justify-content-center align-items-center"
                          style={{
                            height: "29px",
                            width: "32px",
                            backgroundColor: "#FFDADD",
                            borderRadius: "5px",
                            cursor: "pointer",
                          }}
                          onClick={() => {
                            setDeleteModel(true);
                            setDeleteId(3);
                          }}
                        >
                          <RiDeleteBin5Fill
                            style={{
                              color: "#E63946",
                              fontSize: "18px",
                            }}
                          />
                        </div>
                      </div>
                    </div>
                  ) : (
                    <AllergyHistory
                      setEditing={(status) =>
                        setEditing((prev) => ({ ...prev, allergy: status }))
                      }
                    />
                  )}
                  {/* <div className="accordion_content p-3 position-relative">
                      <Form.Group className="mb-3">
                        <Form.Control
                          name="allergyHistory"
                          as="textarea"
                          defaultValue={patientHistoryData?.allergyHistory}
                          rows={12}
                          {...register("allergyHistory")}
                        />
                      </Form.Group>
                    </div>
                    <div className="mb-2 d-flex justify-content-end pe-3">
                      <Button
                        onClick={() => setHistoryType(3)}
                        className="px-4 py-2 mt-2 primary_bg"
                        size="sm"
                        type="submit"
                      >
                        Save{" "}
                      </Button>
                    </div> */}
                </Accordion.Body>
              </Accordion.Item>
            </Accordion>
          </div>
          <div className="d-flex justify-content-end width_75 mx-auto">
            <Button
              onClick={() => navigate(PATH.PERSONAL_INFORMATION)}
              className="d-flex align-items-center px-4 px-md-5 fs-5 py-2 me-2 primary_outline"
              size="sm"
            >
              {" "}
              <AiOutlineLeft className="fs-6 me-1" />
              Back
            </Button>
            <Button
              onClick={() => navigate(PATH.PHARMACY)}
              className="d-flex align-items-center px-4 px-md-5 fs-5 py-2 primary_bg"
              size="sm"
              variant="primary"
            >
              Next <AiOutlineRight className="fs-6 ms-1" />
            </Button>
          </div>
        </div>
      </ProfileLayout>
      {deleteId && (
        <ConfirmationModal
          show={deleteModel}
          onHide={() => setDeleteModel(false)}
          heading={`Delete ${
            deleteId === 1
              ? "Past Medical"
              : deleteId === 2
              ? "Current Medical"
              : "Allergy"
          } History`}
          title={`this ${
            deleteId === 1
              ? "Past Medical"
              : deleteId === 2
              ? "Current Medical"
              : "Allergy"
          } History`}
          removeFunc={onDelete}
        />
      )}
    </>
  );
};

export default MedicalHistory;
