import { useState } from 'react';
import { Link } from 'react-router-dom';
import { FaBars } from "react-icons/fa";
import { PATH } from "../../utils/path.js";
import IMAGES from '../../assets/images';
import ProfileHeader from './ProfileHeader.jsx';

const steps = [
  {
    label: "General Information",
    link: PATH.PERSONAL_INFORMATION
  },
  {
    label: "Medical History",
    link: PATH.MEDICAL_HISTORY
  },
  {
    label: "Pharmacy",
    link: PATH.PHARMACY
  }
];

const ProfileSidebar = ({ activeStep }) => {
  const [isSidebarVisible, setIsSidebarVisible] = useState(false);
  const [completedSteps, setCompletedSteps] = useState([]);

  const toggleSidebar = () => {
    setIsSidebarVisible(!isSidebarVisible);
  };

  const handleStepClick = (index) => {
    if (!completedSteps.includes(index)) {
      setCompletedSteps([...completedSteps, index]);
    }
  };

  return (
    <>
      <button className="sidebar-toggle" onClick={toggleSidebar}>
        <FaBars />
      </button>
      <div className={`profileSidebar ${isSidebarVisible ? 'visible' : ''}`}>
        <aside className='d-flex flex-column justify-content-center align-items-center'>
          <Link
            to={PATH.PATIENT_DASHBOARD}
            className="logo mr-0 header-logo-image text-decoration-none mt-5"
          >
            <img alt="sidebar logo" src={IMAGES.SIDEBAR_LOGO} />
          </Link>
          <ul className='list-unstyled py-3 mt-5 d-flex flex-column justify-content-center align-items-start'>
            {steps.map((step, index) => (
              <li key={index} className='mb-4'>
                <Link
                  to={step.link}
                  className={index === activeStep ? 'active' : completedSteps.includes(index) ? 'completed' : ''}
                  onClick={() => handleStepClick(index)}
                >
                  <span className='p-3 spanBorder rounded-circle me-2'>{`0${index + 1}`}</span>
                  {step.label}
                </Link>
                {index !== steps?.length - 1 && (
                  <span className='d-flex justify-content-start ps-4 mt-4'>
                    <img alt="vertical line" src={IMAGES.VERTICAL_LINE} />
                  </span>
                )}
              </li>
            ))}
          </ul>
        </aside>
      </div>
      <ProfileHeader activeStep={activeStep} steps={steps} />
    </>
  );
};

export default ProfileSidebar;
