import React from "react";
import { Button, Modal } from "react-bootstrap";
import { BsTrash } from "react-icons/bs";

export function ConfirmationModal(props) {
  return (
    <>
      <Modal
        show={props.show}
        onHide={props.onHide}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
        className="appointment-modal"
      >
        <Modal.Header closeButton></Modal.Header>
        <Modal.Body>
          <div className="d-flex justify-content-center">
            <div
              className="p-4 rounded-circle m-auto"
              style={{ background: "#EDEAFD" }}
            >
              <BsTrash className="fw-bold" size="24" style={{color: "#000071"}}/>
            </div>
          </div>
          <h3
            className="text-center mx-auto mt-4"
            style={{ fontWeight: 600 }}
          >
            {props.heading}
          </h3>
          <p className="text-center mt-3 mb-4">
            Are you sure you want to delete {props.title}?
          </p>
          <span className="d-flex justify-content-center">
            <Button
              style={{ background: "#FD2121", border: "none" }}
              className="px-4 mb-3"
              onClick={() => {
                props.removeFunc();
                props.onHide();
              }}
            >
              Delete
            </Button>
          </span>
        </Modal.Body>
      </Modal>
    </>
  );
}
